@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  font-family: 'PT Sans', sans-serif;
}
.App {
  width: 100%;
  height: 100vh;
  background: #000;
  z-index: -2;
}
.max-width {
  max-width: 1200px;
  margin: auto;
}
.animation-area {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 0;
}
.box-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}
.box-area li {
    position: absolute;
    display: block;
    width: 25px;
    height: 25px;
    background-color: rgba(255, 255, 255, 0.2);
    animation: animate 20s linear infinite;
    bottom: -150px;
}
.box-area li:nth-child(1) {
    left: 86%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}
.box-area li:nth-child(2) {
    left: 12%;
    width: 30px;
    height: 30px;
    animation-delay: 1.5s;
    animation-duration: 10s;
}
.box-area li:nth-child(3) {
    left: 70%;
    width: 100px;
    height: 100px;
    animation-delay: 5.5s;
}
.box-area li:nth-child(4) {
    left: 70%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 15s;
}
.box-area li:nth-child(5) {
    left: 65%;
    width: 40px;
    height: 40px;
    animation-delay: 0s;
}
.box-area li:nth-child(6) {
    left: 15%;
    width: 110px;
    height: 110px;
    animation-delay: 3.5s;
}
@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }
    100% {
        transform: translateY(-800px) rotate(360deg);
        opacity: 0;
    }
}
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: auto;
    z-index: 99;
}
.navbar .max-width {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.rep {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.rep i {
    font-size: 2rem;
    color: #fff;
    display: none;
    padding: 10px;
}
.logo h3 {
    font-size: 2rem;
    color: #fff;
    padding: 10px;
}
.nav-menu {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav-menu li {
    padding: 20px;
}

.nav-menu li i:hover {
    color: rgb(180, 180, 180);
}
.nav-item {
    font-size: 1.5rem;
    color: #fff;
    text-transform: uppercase;
}
.active {
    border-bottom: 3px solid deepskyblue;
    color: deepskyblue;
    font-weight: bold;
}

@media only screen and (max-width: 550px) {
    .navbar .max-width {
        display: block;
    }
    .rep i {
        display: block;
    }
    .nav-menu {
        display: none;
        background: #000;
        height: 100vh;
    }
    .show {
        display: block;
    }
}
.home {
    display: flex;
    height: 100vh;
    color: #fff;
    min-height: 500px;
    position: relative;
    z-index: 9;
  }
  .home .max-width {
    margin: auto;
  }
  .home .home-content .text-1 {
    font-size: 32px;
    color: deepskyblue;
  }
  .home .home-content .text-2 {
    font-size: 75px;
    font-weight: 600;
    margin-left: -3px;
  }
  .home .home-content .text-3 {
    font-size: 40px;
    margin: 5px 0;
  }

  @media only screen and (max-width: 550px) {
    .home .home-content {
      padding: 20px 40px;
    }
    .home .home-content .text-2 {
      font-size: 50px;
    }
    .home .home-content .text-3 {
      font-size: 30px;
    }
}

.skill {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 9;
}
.skill .max-width {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill .container h3 {
    display: flex;
    justify-content: center;
    font-size: 3rem;
    color: rgba(255, 255, 255, 0.349);
    text-transform: uppercase;
}
.icon-skills {
    display: flex;
    justify-content: center;
    z-index: 999;
}
.icon-skills i{
    display: inline-flex;
    width: 200px;
    height: 200px;
    align-items: center;
    justify-content: center;
    font-size: 7rem;
    margin: 10px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    background-size: 100% 200%;
    background-position: 0% 0%;
    transition: background-position .5s, color .5s;
}
.icon-skills i:nth-child(1) {
    color: #FF5733;
    background-image: linear-gradient(rgba(255, 255, 255, 0.281) 50%, #FF5733 50%);
}
.icon-skills i:nth-child(2) {
    color: #3091FF;
    background-image: linear-gradient(rgba(255, 255, 255, 0.281) 50%, #3091FF 50%);
}
.icon-skills i:nth-child(3) {
    color: #FFC300;
    background-image: linear-gradient(rgba(255, 255, 255, 0.281) 50%, #FFC300 50%);
}
.icon-skills i:nth-child(4) {
    color: #30C7FF;
    background-image: linear-gradient(rgba(255, 255, 255, 0.281) 50%, #30C7FF 50%);
}
.icon-skills i:hover {
    background-position: 0% 100%;
    color: #fff;
}

@media only screen and (max-width: 550px) {
    .icon-skills {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .icon-skills i {
        width: 100px;
        height: 100px;
        font-size: 4rem;
    }
}


.about {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 9;
}
.about .max-width {
    display: flex;
    align-items: center;
    justify-self: center;
    height: 100vh;
}
.about-container {
    width: 100%;
    display: flex;
    justify-content: space-around;
}
.profile-pic {
    width: 350px;
    height: auto;
}
.profile-pic .img-pro {
    width: 100%;
    height: auto;
    border-radius: 50%;
    border: 10px solid rgba(255, 255, 255, 0.158);
}
.profile-n {
    color: #fff;
}
.profile-n h2 {
    font-size: 2rem;
}
@media only screen and (max-width: 550px) {
    .about-container {
        display: block;
    }
    .profile-pic {
        padding-top: 50px;
        width: 100%;
    }
    .profile-pic .img-pro {
        width: 40%; 
    }
  
   
}

.contact {
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-social {
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-social a {
    display: inline-flex;
    width: 80px;
    height: 80px;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    margin: 10px;
    color: rgba(255, 255, 255, 0.541);
    background: rgba(255, 255, 255, 0.12);
    transition: .4s;
    cursor: pointer;
}
.icon-social a:nth-child(1):hover {
    color: #3498DB;
    background: #fff;
}
.icon-social a:nth-child(2):hover {
    color: #d73deb;
    background: #fff;
}
.icon-social a:nth-child(3):hover {
    color: #000;
    background: #fff;
}
.sub-contact {
    margin: 10px;
}
.sub-contact h2 {
    font-size: 1.4rem;
    color: deepskyblue;
}
.sub-contact span {
    color: rgba(255, 255, 255, 0.562);
    transition: .3s;
}
.sub-contact span:hover {
    color: #fff;
}


@media only screen and (max-width: 550px) {
    .contact {
        display: block;
    }
    .icon-social {
        display: block;
    }
   .icon-social a {
       width: 45px;
       height: 45px;
       font-size: 2rem;
   }
   .sub-contact h2{
    font-size: 1rem;
}
}
.tab-container {
    display: flex;
    flex-direction: column;
    position: relative;
     width: 700px;
    height: 100%;
  
}
.tabs {
    display: flex;
}
.tab {
    padding: 15px;
    text-align: center;
    width: 50%;
    background: rgba(128, 128, 128, 0.075);
    cursor: pointer;
    box-sizing: content-box;
    position: relative;
    outline: none;
}
.tab h2 {
    color: #fff;
    text-transform: uppercase;
}
.active-t {
 background: rgba(255, 255, 255, 0.12);
}
.content-tab {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.12);
}

.tab-con {
  
  padding: 40px;
  width: 100%;
  height: 100%;
  display: none;
}
.tab-con p {
    font-size: 1.9rem;
    padding-bottom: 20px;
}
.tab-con span {
    font-weight: bold;
    margin-right: 20px;
}
.tab-con h3 {
    font-size: 3rem;
    padding-bottom: 20px;
}
.active-c {
    display: block;
}
@media only screen and (max-width: 550px) {
    .tab-container {
        width: 100%;
    }
   
   .tab {
       width: 100%;
   }
   .tab h2 {
       font-size: 1.2rem;
       
   }
   .tab-con {
       padding: 30px;
   }
   .tab-con p {
       font-size: 1rem;
       padding-bottom: 10px;
   }
   .tab-con h3 {
       font-size: 1.5rem;
       padding-bottom: 10px;
   }
}
.projects {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 9;
}
.projects .max-width {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.projects .max-width h1 {
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 3rem;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.616);
}
.projects .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}
.project-item {
    width: 450px;
    transition: .3s;
    cursor: pointer;
} 
.project-item .face{
    width: 100%;
    
}
.project-item .face1 {
    z-index: 1;
    transform: translateY(65px);
    transition: .3s;
    
}
.project-item:hover .face1 {
    transform: translateY(0);
}
.project-item .Proj_pic1, .Proj_pic2, .Proj_pic3 {
    width: 100%;
}
.project-item .face2 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: auto;
}
.project-item .face2 a {
    font-size: 2rem;
    color: #fff;
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
    background: rgba(255, 255, 255, 0.164);
}
.project-item .face2 a:hover {
    background: rgba(0, 0, 0, 0.466);
}

